

import { mapGetters } from 'vuex'
import Loading from 'vux/src/components/loading/index.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'

export default {
name: 'app',
components: {
ViewBox,
XHeader,
Tabbar,
TabbarItem,
Loading
},
data () {
return {
direction: 'slideInLeft'
}
},
computed: {
...mapGetters([
'isLoading'

])
},
watch: {
$route (to, from) {
if (to.path === '/' || to.path === '/checkLoan') {
this.direction = 'slideInLeft'
} else if (to.path === '/my' || to.path === '/more') {
this.direction = 'slideInRight'
} else {
this.direction = 'fadeIn'
}
}
},
mounted () {
},
activated () {
}
}



